<template>
	<div>
		<p class="title">首次登录,请绑定手机号</p>
		<input type="number"  placeholder="输入手机号" class="iphone" v-model="iphone"/>
		
		<div class="code">
			<input type="number"  style="float: left;padding: 0;" placeholder="输入短信验证码" v-model="code" />
			<div class="code_btn" @click="sendCode"><span v-if="!sendCodeStatus" style="color:#fa8a12;">发送验证码</span>
				<span v-if="sendCodeStatus" style="color: #a6a6a6;">{{time}}</span>
				
			</div>
			<div class="error" v-if="iphoneError">
				该手机号已绑定其他的微信账号,请重新输入
				
			</div>
			<div class="error" v-if="codeError">
				验证码有误请重新输入
				
			</div>
		</div>
		
		<div class="binding_btn" @click="go">
			绑定
		</div>
		<div class="agree" >
		<div class="circle">
			<img src="../../assets/right.png"  />
			
		</div> <router-link :to="{ name: 'Agree' }" >
			绑定则代表同意服务协议
		</router-link>
		</div>
		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		data(){
			return {
				iphoneError:false,
				codeError:false,
				sendCodeStatus:false,
				time:60,
				smskey:"",
				code:"",
				iphone:""
			
				
			}
			
		},
		methods:{
			sendCode(){
				
				var _this=this
				var obj={
					"a":1,
					"type":1,
					"mobile":this.iphone
				}
				this.$Api.SmsCode(obj).then((res)=>{
					console.log(res)
					if(res.q.d=="操作成功"){
						this.sendCodeStatus=true
						
					}else{
						 this.$toast.center(res.q.d);
					}
					
				})
				
				var timer=setInterval(function(){
					_this.time-=1
					if(_this.time==0){
						_this.sendCodeStatus=false
						_this.time=60
						clearInterval(timer)
					}
					
					
				},1000)
				
			},
			go(){
				var obj={
					"a":2,
					"type":1,
					"mobile":this.iphone,
					 "code":this.code
				}
				this.$Api.SmsCode(obj).then((res)=>{
					console.log(res)
					this.smskey=res.q.smskey
					this.$Api.BindMobile(this.iphone,this.smskey).then((res)=>{
						console.log(res)
						if(res.q.d=="操作成功"){
							this.$store.state.sessionId=res.s
						this.setCookie("sessionId",res.s,15)
						 
						this.$router.push({
							name:"Home"
						})
						}else{
							if(res.q.d=="q.sms key 不能为空"){
								this.$toast.center("验证码已过期");
							}else{
								this.$toast.center(res.q.d);
								var that=this;
								setTimeout(function(){
									that.$router.push({
									name:'Extension'
								})
									
								},500)
								
							}
						}
						
						
						
						
					})
					
					
				})
				
				
			},
			 setCookie(cname, cvalue, exdays) {
				var d = new Date();
				d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
				var expires = "expires=" + d.toGMTString();
				document.cookie = cname + "=" + cvalue + "; " + expires;
			}
			
			
			
		}
		
	}
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
.title{
    font-size: 0.3rem;
    padding: 0.4rem 0.3rem 0.3rem 0.3rem;
    float: left;
}
input{
    float: left;
     text-indent:0.2rem ;
}
.iphone{
    width: 6.9rem;
    height: 0.8rem;
    border: 0.02rem solid #cccccc;
    border-radius:0.1rem ;
  
    margin-left: 0.3rem;
    margin-bottom: 0.3rem;
}
.code{
      float: left;
     width: 6.9rem;
    height: 0.8rem;
    border: 0.02rem solid #cccccc;
    padding: 0.2rem 0;
    box-sizing:  border-box;
    position: relative;
     border-radius:0.1rem ;
     margin-left: 0.3rem;
     input{
         width: 4.5rem;
         height: 0.4rem;
        border: none;
        text-indent:0.4rem ;
        border-right:0.02rem solid #cccccc ;
     }
      .code_btn{
          width: 2.3rem;
          float: right;
          font-size: 0.3rem;
       
          text-align: center;
          line-height: 0.4rem;
      }
      .error{
      	width: 100%;
      	position: absolute;
      	bottom: -0.6rem;
      	color: #FA8A12;
      	font-size: 0.28rem;
      }
      
}
.binding_btn{
    width: 6.9rem;
    height: 0.9rem;
    text-align: center;
    color: #FFFFFF;
    line-height: 0.9rem;
    background: #FA8A12;
    float: left;
    font-size: 0.34rem;
    margin-left: 0.3rem;
    border-radius:0.45rem ;
    margin-top: 1.4rem;
}

.agree{
 width: 100%;padding:0.4rem 0.35rem ;
 float: left;
    
    .circle{
    width: 0.36rem;
    height: 0.36rem;
    float: left;
    background: #FA8A12;
    position: relative;
    border-radius:50% ;
    margin-right: 0.25rem;
    img{
        width: 0.24rem;
        height: 0.17rem;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
    }
} 
    a{
        float: left;
        color:#FA8A12 ;
        font-size: 0.24rem;
        text-decoration: underline;
    }
    
}







</style>


